import React from 'react'

import './NavBar.scss'

const links = [
  {
    link: '/home',
    title: '',
  },
  {
    link: '/menu',
    title: '',
  },
  {
    link: '/afisha',
    title: '',
  },
  {
    link: '/collaboration',
    title: '',
  },
  {
    link: '/gallery',
    title: '',
  },
  {
    link: '/news',
    title: '',
  },
]

const NavBar = () => {
  return (
    <nav className='navbar'>
      <ul className='navbar__link-list'>
        {links.map((link) => {
          return (
            <li key={link.link} className='navbar__list-item'>
              <a className='navbar__link' href={link.link}>
                {link.title}
              </a>
            </li>
          )
        })}
      </ul>
    </nav>
  )
}

export default NavBar
